.nav-tabs.wizard {
    background-color: transparent;
    padding: 0;
    width: 100%;
    margin: 1em auto;
    clear: both;
    border-bottom: none;
    @include rounded(.25em);
}

.nav-tabs.wizard li {
    width: 100%;
    float: none;
    margin-bottom: 3px;
}

.nav-tabs.wizard li.disabled {
    display: none;
}

.nav-tabs.wizard li>* {
    position: relative;
    padding: 1.3em .8em .5em 2.5em;
    color: #999999;
    background-color: #dedede;
    border-color: #dedede;

    &:focus,
    &:hover {
        color: #999999;
        background-color: #dedede;
        border-color: #dedede;
    }
}

.nav-tabs.wizard li.completed>* {
    color: #fff !important;
    background-color: #96c03d !important;
    border-color: #96c03d !important;
}

.nav-tabs.wizard li.active>* {
    color: #fff !important;
    background-color: #2c3f4c !important;
    border-color: #2c3f4c !important;
}

.nav-tabs.wizard li.error>* {
    color: #fff !important;
    background-color: #c03d3d !important;
    border-color: #c03d3d !important;
}

.nav-tabs.wizard li::after:last-child {
    border: none;
}

.nav-tabs.wizard>li>a {
    opacity: 1;
    font-size: 14px;
}

.nav-tabs.wizard a:hover {
    color: #fff;
    background-color: #2c3f4c;
    border-color: #2c3f4c
}

span.nmbr {
    display: inline-block;
    padding: 9px 0 0 0;
    background: #ffffff;
    width: 35px;
    line-height: 100%;
    height: 35px;
    margin: 0 1em 0 0;
    font-weight: bold;
    font-size: 16px;
    color: #555;
    margin-bottom: 10px;
    text-align: center;
    @include rounded(50%);
    @include box-shadow(0, 0, 10px, rgba(0, 0, 0, 0.25));
}

@media(min-width:992px) {
    .nav-tabs.wizard li {
        position: relative;
        padding: 0;
        margin: 0;
        width: 20%;
        float: left;
        text-align: center;
    }

    .nav-tabs.wizard li::after,
    .nav-tabs.wizard li>*::after {
        content: '';
        position: absolute;
        top: 0px;
        left: 100%;
        height: 0;
        width: 0;
        border: 35.7px solid transparent;
        border-right-width: 0;
        /*border-left-width: 20px*/
    }

    .nav-tabs.wizard li::after {
        z-index: 1;
        /*
        -webkit-transform: translateX(4px);
        -moz-transform: translateX(4px);
        -ms-transform: translateX(4px);
        -o-transform: translateX(4px);
        transform: translateX(4px);
        */
        border-left-color: #fff;
        margin: 0;
        top: 1px;
    }

    .nav-tabs.wizard li>*::after {
        z-index: 2;
        border-left-color: inherit
    }

    .nav-tabs.wizard>li:nth-of-type(1)>a {
        border-top-left-radius: 10px;
        border-bottom-left-radius: 10px;
    }

    .nav-tabs.wizard li:last-child a {
        border-top-right-radius: 10px;
        border-bottom-right-radius: 10px;
    }

    .nav-tabs.wizard li:last-child {
        margin-right: 0;
    }

    .nav-tabs.wizard li:last-child a:after,
    .nav-tabs.wizard li:last-child:after {
        content: "";
        border: none;
    }

    span.nmbr {
        /*display: block;*/
    }
}

.step-app .step-content>.step-tab-panel {
    display: none;
}

.step-app .step-content>.step-tab-panel.disabled {
    display: none;
}

.step-app .step-content>.step-tab-panel.active {
    display: block;
}